    top: 130px;
  }
  .book__face--top {
    transform: rotateX(90deg) translateZ(142.5px);
  }
  .book__face--bottom {
    transform: rotateX(-90deg) translateZ(142.5px);
  }
  .book__face--front {
    transform: rotateY(0deg) translateZ(20px);
  }
  .book__face--back {
    transform: rotateY(180deg) translateZ(20px);
  }
  .book__face--left {
    transform: rotateY(-90deg) translateZ(110px);
  }
  .book__face--right {
    transform: rotateY(90deg) translateZ(102.5px);
  }
  .Book {
    transform: translateZ(-50px);
    transform: rotateZ(30deg) rotateY(-30deg) rotateX(-20deg);
  }
  .book__face--right {
    height: 285px;
    top: 7.5px;
  }
}
